// PRODUCT

.product {
  display: flex;
  flex-wrap: wrap;
  > a {
    flex-basis: 100%;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
    min-height: 200px;
    height: 200px;
    img {}
    &:hover {
      text-decoration: underline;
    }
  }
  > p {
    flex-basis: 100%;
    border-top: 1px solid $app-color-delimiter;
    padding-top: 10px;
    a {
      text-decoration: none;
      @include headfont;
      font-size: $fontsize-h5;
      &:hover {
        text-decoration: underline;
      }
    }
  }
  .price {
    flex-basis: 50%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-right: 10px;
    .price_new {
      font-size: 1.15em;
      @include redularfont;
      color: $app-color-black;
      white-space: nowrap;
    }
  }
  .product-item-info-container {
    flex-basis: 50%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin: 0;
    padding-left: 10px;
    a.btn {
      background-color: $app-color-primary;
      &:hover,
      &:focus {
        background-color: $app-color-secondary;
      }
    }
  }

}


// PRODUCT ITEM

.product-item {
  padding: 15px 20px;
  display: flex;
  flex-wrap: wrap;
  background-color: $app-color-white;
  margin-bottom: 20px;
  @include br-small;
  &:hover {
    @include shadow-hover;
  }
  &-image-wrapper {
    flex-basis: 100%;
    width: 100%;
    min-height: 200px;
    height: 200px;
    display: flex;
    padding-bottom: 15px;
    border-bottom: 1px solid $app-color-delimiter;
    transition: filter 0.3s ease-in-out;
    &:hover {
      span {
        filter: brightness(1.1);
      }
    }
    span {
      width: 100%;
      height: 100%;
      display: block;
      background-size: cover;
      background-repeat: no-repeat;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
  }
  &-image-alternative {
    display: none !important;
  }

  &-title {
    flex-basis: 100%;
    padding-top: 10px;
    padding-bottom: 20px;
    a {
      color: $app-color-black;
      text-decoration: none;
      &:hover {
        color: $app-color-black;
        text-decoration: underline;
      }
    }
  }

  &-info-container {
    &[data-entity="price-block"] {
      flex-basis: 50%;
      display: flex;
      align-items: center;
      justify-content: flex-start;
    }
    &[data-entity="buttons-block"] {
      flex-basis: 50%;
      display: flex;
      align-items: center;
      justify-content: flex-end;
    }
  }

  &-price-current {
    font-size: $fontsize-normal !important;
  }

}
